export default {
	data() {
		return {
			imgUrl: this.$imgUrl,
			courseList: [],
		}
	},
	onLoad:function(option) {
		this.gettingData(option.id)
	},
	methods: {
		// 视频播放报错
		videoErrorCallback: function(e) {
			// uni.showModal({
			// 	content: e.target.errMsg,
			// 	showCancel: false
			// })
		},
		// url切割
		getCaption(obj) {
			var index = obj.lastIndexOf("vid=");
			obj = obj.substring(index + 4, obj.length);
			return obj;
		},
		gettingData (id) {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_courses_details',
					'\r\n--XXX' +
					'\r\nContent-Disposition: form-data; name="id"' +
					'\r\n' +
					'\r\n' + id +
					'\r\n--XXX--').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.courseList = res.data.data
					if (res.data.data.type == 2) {
						this.courseList.url = this.getCaption(res.data.data.url)
					}
					console.log(this.courseList)
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
	}
}
